@font-face {
  font-family: Trocchi;
  src: local(Trocchi), url("Trocchi-Regular.112580d1.ttf");
}

html {
  box-sizing: border-box;
}

body {
  color: #000;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Noto Sans, Open Sans, Arial, sans-serif;
  overflow: hidden;
}

p {
  margin: 0 0 20px;
}

*, :before, :after {
  box-sizing: inherit;
}

@keyframes fade-in {
  from {
    opacity: 0;
    pointer-events: none;
  }

  to {
    opacity: 1;
    pointer-events: inherit;
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    translate: 0 10vh;
  }

  to {
    opacity: 1;
    translate: 0;
  }
}

.modal {
  z-index: 3;
  background-color: #ccf;
  border: 5px solid #aae;
  border-radius: 50px;
  margin: 50px;
  padding: 0 50px;
  font-size: 25px;
  animation: .5s slide-in;
  overflow: auto;
}

.modal__bg {
  z-index: 2;
  background-color: #000;
  width: 100%;
  height: 100%;
  animation: .5s fade-in;
  position: fixed;
  overflow-y: auto;
}

.modal__title {
  align-items: top;
  margin-top: 10px;
  display: flex;
}

.modal__title__heading {
  flex: auto;
  margin-block: 0 20px;
  font-family: Trocchi, serif;
  font-size: 60px;
  font-style: normal;
  font-weight: 400;
}

.modal__title__close {
  cursor: pointer;
  margin-left: 1em;
  background: none;
  border: 0;
  flex: none;
  width: 50px;
  height: 50px;
  margin-block: 10px 20px;
}

.modal__title__close__icon {
  width: 100%;
  height: 100%;
}

.modal__org-plug {
  margin-bottom: 20px;
}

.modal__org-plug__logo {
  float: left;
  max-width: 50%;
  margin: 0 2em 20px 0;
}

.modal__org-plug__logo__img {
  width: 100%;
  height: auto;
}

.modal__start-btn {
  cursor: pointer;
  color: #000;
  background-color: #aae;
  border: none;
  width: 100%;
  margin-block: 20px;
  font-family: Trocchi, serif;
  font-size: 40px;
  display: block;
}

.modal__start-btn--smaller {
  font-size: 28px;
}

.modal__start-btn--disabled {
  cursor: auto;
  background-color: #ddd;
}

.modal__link {
  color: #00e;
  text-decoration: underline;
}

.modal__field-row {
  margin-bottom: 20px;
  display: block;
}

.modal__field-row__label {
  margin-bottom: 10px;
  font-weight: bold;
  display: block;
}

.modal__field-row__widget {
  background-color: #fff;
  border: 2px solid #aae;
  border-radius: 10px;
  width: 100%;
  padding: .2em .4em;
  font-size: 1.5em;
  display: block;
}

.modal__divider {
  border: 1px solid #aae;
  margin-block: 30px;
}

.modal__application-notice {
  clear: both;
  font-style: italic;
}

@media (width <= 700px) {
  .modal__title__heading {
    font-size: 32px;
  }

  .modal__title__close {
    width: 40px;
    height: 40px;
  }

  .modal {
    border-radius: 20px;
    margin: 25px;
    padding: 0 25px;
    font-size: 16px;
  }

  .modal__start-btn {
    margin-block: 10px;
    font-size: 32px;
  }

  .modal__start-btn--smaller {
    font-size: 28px;
  }
}

@media (width <= 450px) {
  .modal__title__heading {
    font-size: 24px;
  }

  .modal__title__close {
    width: 30px;
    height: 30px;
  }

  .modal {
    font-size: 16px;
  }

  .modal__start-btn {
    font-size: 28px;
  }

  .modal__start-btn--smaller {
    font-size: 24px;
  }
}

.hidden {
  display: none;
}
